@import "../../responsive.scss";

.layout{
    height: 100vh;
    max-width: 1366px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-direction: column;

    @include lg{
        background-color: beige;
        max-width: 1280px;
    }

    @include md{
        background-color: aquamarine;
        max-width: 768px;
    }

    @include sm{
        background-color: aqua;
        max-width: 640px;
    }

    .content{
        flex: 1;
    }
}
